<script>
export default {
  name: "Comment",
  data() {
    return {
      student: {},
      comment: null,
      comments: [
        {
          notice: "nice",
          sex: -1
        },
        {
          notice:
            " ",
          sex: 0
        },
        {
          notice: "",
          sex: 1
        },
        {
